nav {
	padding: 0.5rem;
	background: var(--basic-1);
	border-top: 1px solid var(--general-1);
}

a.tab,
button.tab {
	min-width: 59px;
	padding: 0;

	div.link-content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		row-gap: 0.25rem;
		column-gap: 1rem;

		mat-icon {
			color: var(--general-4);
		}

		span {
			display: block;
			color: var(--general-3);
		}
	}

	&.active div.link-content {
		mat-icon,
		span {
			color: var(--prim-2);
		}
	}
}
